#root > .wrapper{
/*  min-width: calc(100vw);*/
  height: calc(100vh);
}

.wrapper{
  display: flex;
  width: 100%;
  height: 100%;
  flex-direction: column;
}

.AppSpace {
  height: 100%;
  overflow: hidden;
}

.Appbar{
  height: 64px;
  border-bottom: 1px solid #e0e0e0;
  padding: 0 5px;
  display: flex;
  align-items: center;
}

.Appbar-Right{
  margin-left: auto;
}

.Appbar-Right-Block{
  display: flex;
}

.Appbar-Right-Block > div{
  padding: 0 5px;
}

.AppContainer{
  flex: 1;
  display: flex;
  height: 100%;
  overflow: hidden;
}

.AppContainer .AppContent{
  flex: 1;
  height: 100%;
/*  overflow: auto;*/
}

.AppContainer .AppSidenav{
  width: 250px;
  height: 100%;
  overflow: auto;
}

a{
  text-decoration: none;
}
a.selected{
  font-weight: bold;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.jss5897 {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    position: fixed;
    background: #fafafa;
}

.jss5901.jss5899 {
    height: 400px;
}

.jss5902 {
    background-image: linear-gradient(-45deg, #2196F3 0%, #2196F3 33%, #00BFA5 100%);
    background-attachment: fixed;
}

.jss5901 {
    top: 0;
    left: 0;
    width: 100%;
    position: absolute;
    background-attachment: fixed;
}

.jss5904 {
    left: -2px;
    width: 100%;
    bottom: -32px;
    height: auto;
    position: absolute;
    transform: scale(1.1,0.8);
    transform-origin: bottom;
}
